<template>
  <div class="hot">
    <van-nav-bar
      title="热门推荐"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <van-tabs @change="getid"  v-model="active">
      <van-tab
        v-for="item in tabslist"
        :key="item.id"
        :title="item.title"
        :name="item.id"
      >
        <ul class="tab-ul">
          <li class="ul-li" v-for="(item, i) in typedata" :key="i">
            <router-link :to="{ path: '/about', query: { url: item.url } }">
              <div class="li-img">
                <img :src="item.cover" alt="" />
              </div>

              <div class="list-nrong">
                <div class="nrong-item">
                  <p class="nametxt">
                    <van-icon name="user-o" />
                    {{ item.name }}
                  </p>
                  <p class="txt">
                    <van-icon name="underway-o" />
                    {{ item.time }}
                  </p>
                  <p class="txt">
                    <van-icon name="label-o" />
                    {{ item.latest }}
                  </p>
                  <div class="zi" :class="sstt[i]">
                    {{ i + 1 }}
                  </div>
                </div>
              </div>
            </router-link>
          </li>
        </ul>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import "../assets/scss/hot.scss";
import { mhfleiid } from "@/api/api";

export default {
  data() {
    return {
      active: '',
      tabslist: [
        {
          title: "热门榜",
          id: "hot",
        },
        {
          title: "飙升榜",
          id: "dalu",
        },
        {
          title: "新作榜",
          id: "lianzai",
        },
        {
          title: "完结榜",
          id: "wanjie",
        },
      ],
      typedata: [],
      sstt: ["ss1", "ss2", "ss3"],
    };
  },
created(){
  this.init();
},
  methods: {
    init(){
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let {typeid} = this.$route.query;
     
      this.active = typeid;
       mhfleiid(this.active).then((res) => {
        let { code } = res.data;
        if (code === 0) {
          
          
          this.typedata = res.data.list.slice(1);
        }
        this.$toast.clear();
      });
    },

    back() {
      this.$router.push({ path: "/" });
    },
    getid(tid) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      
      mhfleiid(tid).then((res) => {
        let { code } = res.data;
        if (code === 0) {
          
          
          this.typedata = res.data.list.slice(1);
        }
        this.$toast.clear();
      });
    },
  },
};
</script>

<style lang="scss" scoped>
</style>